export class ControlBase<T> {
  value: T;
  key: string;
  label: string;
  required: boolean;
  order: number;
  controlType: string;
  position: string;
  placeholder: string;
  stylelabel: string;
  labelsizefondt: string;
  disabled: string;
  hidden: string;
  fondoetiqueta: string;
  coloretiqueta: string;
  blike: string;
  type: string;
  rangomin: string;
  rangomax: string;
  rangostep: string;
  alfatypo: string;
  idformulario: string;
  idcampo: string;
  combofijo: string;
  actualiza: string;
  idfrmconsulta: string;
  idfrmconsulta2: string;

  idfrmconsulta2origen: string;
  idfrmconsultaorigen: string;
  usarunaves: string;
  idcampofiltro: string;
  idcampofiltro2: string;
  idcampofiltromanual: string;
  regitrosacumulados: string;
  verregistros: string;
  default: string;
  default2: string;
  default3: string;
  idcampofiltropapa: string;
  idfrmconsultaorigenpapa: string;


  constructor(options: {
    value?: T,
    key?: string,
    label?: string,
    required?: string,
    order?: number,
    type?: string,
    position?: string ,
    placeholder?: string,
    labelsizefondt?: string,
    disabled?: string,
    hidden?: string,

    default?: string,
    default2?: string,
    default3?: string,

    fondoetiqueta?: string,
    coloretiqueta?: string,
    blike?: string,
    rangomin?: string;
    rangomax?: string;
    rangostep?: string;
    alfatypo?: string;
    idformulario?: string;
    idcampo?: string;
    combofijo?: string;
    actualiza?: string;
    idfrmconsulta?: string;
    idfrmconsulta2?: string;
    idfrmconsulta2origen?: string;
    idfrmconsultaorigen?: string;
    usarunaves?: string;
    idcampofiltro?: string;
    idcampofiltro2?: string;
    idcampofiltromanual?: string;
    regitrosacumulados?: string;
    verregistros?: string;
    idcampofiltropapa?: string;
    idfrmconsultaorigenpapa?: string;


  } = {}) {
    this.value = options.value;
    this.key = options.key || '';
    this.label = options.label || '';
    this.required = (options.required == 'true');
    this.order = options.order === undefined ? 1 : options.order;
    this.controlType = options.type || '';
    this.position = options.position || '';
    this.placeholder = options.placeholder || '';

    this.default = options.default || '';
    this.default2 = options.default2 || '';
    this.default3 = options.default3 || '';


    this.stylelabel = 'font-size: 24px !important;';
    this.disabled = '' + options.disabled  + '' || 'false';
    this.hidden = '' + options.hidden + '' || 'false';
    this.blike = '' + options.blike + '' || 'false';
    // background-color:red;color:white
    this.fondoetiqueta = options.fondoetiqueta || '';
    this.coloretiqueta =  options.coloretiqueta || '';

    this.rangomin = '' + options.rangomin + '' || '0';
    this.rangomax = '' + options.rangomax + '' || '1000';
    this.rangostep = '' + options.rangostep + '' || '100';
    this.alfatypo = options.alfatypo || '';
    this.idformulario = options.idformulario || '';
    this.idcampo = options.idcampo || '';
    this.combofijo = options.combofijo || '';
    this.actualiza = options.actualiza || '';
    this.idfrmconsulta = options.idfrmconsulta || '';
    this.idfrmconsulta2 = options.idfrmconsulta2 || '';

    
    this.idfrmconsulta2origen = options.idfrmconsulta2origen || '';
    this.idfrmconsultaorigen = options.idfrmconsultaorigen || '';
    this.usarunaves = options.usarunaves || '';
    this.idcampofiltro = options.idcampofiltro || '';
    this.idcampofiltro2 = options.idcampofiltro2 || '';
    this.idcampofiltromanual = options.idcampofiltromanual || '';
    this.regitrosacumulados = options.regitrosacumulados || '';
    this.verregistros = options.verregistros || '';

    this.idfrmconsultaorigenpapa = options.idfrmconsultaorigenpapa || '';
    this.idcampofiltropapa = options.idcampofiltropapa || '';
    
   // this.coloretiqueta =  'caca' || '';
    // 'background-color:red,color:white;'
// options.fondoetiqueta || '';
   // [ngClass]="control.fondoetiqueta"
   // [style.backgroundColor]="control.fondoetiqueta"

   // [ngClass]="[control.fondoetiqueta,control.coloretiqueta]" style='color:red'    sirve

  }
}
