keys: any = [];
  public getKeys(data) {
    let re = '';
    if (data !== '') {
    if (data.indexOf('°') < 0) {
      re = re + '<br>' +  data.split('¬')[0]  + ''; // &nbsp;
    } else {
    this.keys = data.split('°');

    re = '<br>'
    for (let i = 0; i < this.keys.length; i++) {
      const aa = this.keys[i].split(':');
      if (aa[0] !== '' && String(aa[0]).indexOf('¬') < 0) {
       if (aa[1] === undefined) {
          re = re + '' +  aa[0]  + '<br>'; // &nbsp;
        } else {
          re = re + '<strong>' +  aa[0]  + '</strong> : ' + aa[1] + '<br>'; // &nbsp;
        }
      }
        }


      }
      }

    return re;
  }

